<template>
  <div class="fillcontain">
    <head-top></head-top>
    <div class="table_container">
      <el-table :data="tableData4" style="width: 100%">
        <el-table-column fixed prop="date" label="" width="150"></el-table-column>
        <el-table-column prop="name" label="" width="120"></el-table-column>
        <el-table-column prop="province" label="" width="120"></el-table-column>
        <el-table-column prop="city" label="" width="120"></el-table-column>
        <el-table-column prop="address" label="" width="300"></el-table-column>
        <el-table-column prop="zip" label="" width="120"></el-table-column>
        <el-table-column  label="" width="120">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData4)"
              type="danger"
              size="small"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import headTop from "../public/headTop";
export default {
  methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
        this.$message.success('');
      }
    },
    data() {
      return {
        tableData4: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-08',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-06',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-07',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    },
    components: {
        headTop
    }
};
</script>

<style lang="less">
@import "../style/mixin";
.table_container {
  padding: 20px;
}
</style>



