<template>
  <div class="fillcontain">
    <head-top></head-top>
    <div class="table_container">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="expand">
          <template>
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ name }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ address }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ description }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ id }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ phone }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ rating }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ recent_order_num }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ category }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="address"></el-table-column>
        <el-table-column label="" prop="description"></el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button size="small" @click="edit"></el-button>
            <router-link to="/addGoods">
              <el-button size="small"></el-button>
            </router-link>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, tableData)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="selectTable">
          <el-form-item label="" label-width="100px">
            <el-input v-model="selectTable.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
              <el-input v-model="selectTable.address" auto-complete="off"></el-input>
              <span>{{city.name}}</span>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input v-model="selectTable.description"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input v-model="selectTable.phone"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select placeholder="" v-model="selectTable.classification">
              <el-option label="" value="123"></el-option>
              <el-option label="" value="456"></el-option>
              <el-option label="" value="123"></el-option>
              <el-option label="" value="456"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="updateShop"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import headTop from "../public/headTop";
export default {
  data() {
    return {
      city: {
        name:""
      },
      dialogFormVisible: false,
      selectTable: {
        name:"",
        description:"",
        phone:"333-444-555",
        address:"219F1"
      },

      tableData: [
        {
          description: '',
          name: '',
          address: ' 1518 ',
          id:1
        }, 
        {
          description: '',
          name: '',
          address: ' 1517 ',
          id:2
        },
        {
          description: '',
          name: '',
          address: ' 1519 ',
          id:3
        }, 
        {
          description: '',
          name: '',
          address: ' 1516 ',
          id:4
        }
      ],

      name:"",
      address:"219F1",
      description:"",
      id:'007',
      phone:'333-444-555',
      rating:"",
      recent_order_num:"23001/",
      category:"",

      
    };
  },
  components: {
    headTop
  },
  methods: {
    handleDelete(index, rows) {
      rows.splice(index, 1);
      this.$message.success('');
    },
    updateShop() {
      this.dialogFormVisible = false;
    },
    edit(){
      this.dialogFormVisible = true;
      // console.log(123);
    }
}
}
</script>

<style lang="less" scoped>
@import "../style/mixin";
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.table_container {
  padding: 20px;
}
.Pagination {
  display: flex;
  justify-content: flex-start;
  margin-top: 8px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar {
  width: 120px;
  height: 120px;
  display: block;
}
.el-button--small, .el-button--small.is-round {
    padding: 5px 8px;
}
</style>



